.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.phonebook {
  color: blue;
  animation: phone 500ms ease-in alternate-reverse;
}

.phonebook-enter {
  opacity: 0;
}

.phonebook-enter-active {
  transition: opacity 500ms ease-out;
  opacity: 1;
}

/* .phonebook-exit-active {
  opacity: 0;
} */

@keyframes phone {
  0% {
    transform: translate(150px);
  }
  30% {
    transform: translate(-150px);
    opacity: 0.7;
  }
  100% {
    transform: translate(250px);
    opacity: 0;
  }
}
